<script>
import { defineComponent } from '@vue/composition-api'


export default defineComponent({
    setup() {

    },
    data: () => ({

    }),
    props: ['card'],
    mounted(){
        console.log(this.card)
    },
    methods: {
        getImageLink() {
            const card = this.card
            console.log('getImageLink', this.card)
            if (card?.image !== null && card?.image !== undefined) {
                // console.log(location.origin+card.image)
                let hostname = this.$globalConfig.getHostName()
                return `${hostname}/news-photo/${card.image}`;
                // return card.image
            }
            return ''
        }
    }
})
